.formInput{
    display: flex;
    flex-direction: column;
    width: 280px;
}

input{
    padding: 15px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid gray;
    font-style: arial;
}


label{
    font-size: 12px;
    font-style: arial;
    color: rgb(255, 255, 255);
}

span{
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none;
    font-style: arial;
}

input:invalid[focused="true"]{
    border: 1px solid red;
}

input:invalid[focused="true"] ~ span{
    display: block;
}